import { Typography } from "@mui/material";
import Button from '@mui/material/Button';
import StarIcon from "@mui/icons-material/Star";
import CalendarMonthOutlinedIcon from '@mui/icons-material/CalendarMonthOutlined';
import SensorDoorOutlinedIcon from '@mui/icons-material/SensorDoorOutlined';
import { Bottom, BottomLeft, BottomRight, Container, ContainerRight, PriceLeft, PriceRight, PriceRow, RightBottom, RightMid, RightPrice, RightTop, RightTopLeft, RightTopRight } from './index.sty'
import { useNavigate } from "react-router-dom";
import { useContext, useState } from "react";
import { getNoOfNight } from "../../utils";
import { LoginContext } from "../../Contexts/LoginContext";
import DateRoomsAdultCount from "../DateRoomsAdultCount";
const CardRight = (props) => {
    const { searchParams, userProfileDetails } = useContext(LoginContext);
    console.log("searchParams:", searchParams);

    const navigate = useNavigate();
    const { hotelData, roomsDetails, buttonMsg } = props;
    console.log("..roomsDetails..", roomsDetails?.rooms);
    console.log("totalRoomPriceDetails", roomsDetails?.roomsPriceDetails);
    console.log("mendatoryRoomServiceArray", roomsDetails?.mendatoryRoomServiceArray);
    function onBookNowClick() {
        if(!userProfileDetails.phone_number) {
            navigate("/login", {state: {data: {redirectPage: 'hotelpage'}}});
        } else {
            navigate("/booking", { state: { data: { searchParams, hotelData, roomsDetails } } });
        }
    }
    if (hotelData && roomsDetails && searchParams) {
        const { rooms, roomsPriceDetails } = roomsDetails;
        return (
            <Container>
                <ContainerRight>
                    <RightTop>
                        <RightTopLeft>
                            <Typography style={{ fontWeight: 'bold' }}>{hotelData.hotelname}</Typography>
                            <Typography style={{ color: '#e4e4e4' }}>{hotelData.locality.locality_name}</Typography>
                            <div className="left-rating" style={{ display: 'flex', flexWrap: 'wrap', margin: '10px 5px' }}>
                                <div style={{ flex: '1 1 auto' }}>
                                    <Button variant="contained" color="success" endIcon={<StarIcon />}>
                                        {hotelData.rating_json.rating}
                                    </Button>
                                </div>
                                <div style={{ flex: '1 1 auto', textAlign: 'right' }}>
                                    <p className="rating-words">{hotelData.rating_json.rating_in_words}</p>
                                    <p className="ratings-count">{hotelData.rating_json.no_of_people_gave_this_rating} ratings</p>
                                </div>
                            </div>
                            <Typography style={{ fontWeight: 'bold' }} >{searchParams?.to_date?.getDate() - searchParams?.from_date?.getDate() + 1} Night</Typography>
                        </RightTopLeft>
                        <RightTopRight>
                            <img
                                alt='img'
                                width='100%'
                                src={hotelData?.hotel_gallery[0]?.file}
                                style={{
                                    marginBottom: 'auto',
                                    marginLeft: 'auto'
                                }}
                            />
                        </RightTopRight>
                    </RightTop>
                    <RightMid>
                        <DateRoomsAdultCount />
                        <hr style={{ margin: '1rem auto' }} />
                        <Typography style={{ fontWeight: 'bold', display: 'flex', alignItems: 'center' }}><SensorDoorOutlinedIcon style={{ marginRight: '1rem' }} /> Classic</Typography>
                    </RightMid>
                    <RightPrice>
                        <PriceRow>
                            <PriceLeft>
                                Room price for <span style={{ fontStyle: "italic", opacity: '0.5' }}>{searchParams?.room_count} Room X {getNoOfNight(searchParams?.from_date, searchParams?.to_date)} Night X {searchParams?.no_of_adults} Guests </span>
                            </PriceLeft>
                            <PriceRight>
                                ₹{roomsPriceDetails?.totalHotelSumBeforeDisCount}
                            </PriceRight>
                        </PriceRow>
                        {roomsPriceDetails?.mendatoryRoomServiceArray ?
                            roomsPriceDetails.mendatoryRoomServiceArray.map((mendatoryRoomServiceObj) => {
                                return <PriceRow>
                                    <PriceLeft>
                                        {mendatoryRoomServiceObj.serviceName} <span style={{ fontStyle: "italic", opacity: '0.5' }}>{getNoOfNight(searchParams?.from_date, searchParams?.to_date)} Night</span>
                                    </PriceLeft>
                                    <PriceRight>
                                        +₹{mendatoryRoomServiceObj.grossAmount}
                                    </PriceRight>
                                </PriceRow>
                            }) : <></>
                        }
                        {roomsPriceDetails?.discountArray ?
                            roomsPriceDetails.discountArray.map((discountObj) => {
                                if (discountObj.discountAmount > 0) {
                                    return <PriceRow>
                                        <PriceLeft>
                                            <span>{discountObj.discountName}</span> <span style={{ fontStyle: "italic", opacity: '0.5' }}>Discount</span>
                                        </PriceLeft>
                                        <PriceRight>
                                            -₹{discountObj.discountAmount.toFixed(2)}
                                        </PriceRight>
                                    </PriceRow>
                                }

                            }) : <></>
                        }
                    </RightPrice>
                    <hr style={{ width: '90%', margin: '2rem auto' }} />
                    <RightBottom>
                        <BottomLeft>
                            <Typography style={{ fontSize: '2rem' }} >Payable Amount</Typography>
                            <Typography style={{ color: 'gray', fontSize: '1.25rem' }} >inclusive of all taxes</Typography>
                        </BottomLeft>
                        <BottomRight>
                            ₹{roomsPriceDetails?.totalHotelSumAfterDisCount}
                        </BottomRight>
                    </RightBottom>
                    {buttonMsg !== ''? <Bottom style={{ backgroundColor: 'gray', color: '#fff', cursor: "pointer" }} disabled>
                        {buttonMsg}
                    </Bottom> : 
                    <Bottom style={{ backgroundColor: '#1ab64f', color: '#fff', cursor: "pointer" }} onClick={() => onBookNowClick()}>
                    Continue to Book
                </Bottom>
                    }
                    <Bottom>
                        All staff vaccinated with 1st dose
                    </Bottom>
                </ContainerRight>
            </Container>
        )
    }
}
export default CardRight;