import React from "react";
import ButtonPrimary from "../../components/button/ButtonPrimary";
import appStoreBadge from "../../media/images/app_store_badge.png";
import googlePlayBadge from "../../media/images/google_play_badge.png";
import ApartmentIcon from "@mui/icons-material/Apartment";
import InstagramIcon from "@mui/icons-material/Instagram";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import YouTubeIcon from "@mui/icons-material/YouTube";
import logoImg from "../../media/images/logo.png";

const Footer = () => {
  return (
    <div className="footer">
      <div className="sec1">
        <div className="left">
          <div className="action">
            <h1>Join our network and grow your business!</h1>
            <ButtonPrimary text="List your property" icon={<ApartmentIcon />} />
          </div>
        </div>
        <div className="right">
          <div className="desc">
            <h3>Download WB Hotels app for exciting offers.</h3>
          </div>
          <div className="badges">
            <div className="app-store-badge">
              <img src={appStoreBadge} alt="app store badge" />
            </div>
            <div className="google-play-badge">
              <img src={googlePlayBadge} alt="google play badge" />
            </div>
          </div>
        </div>
      </div>

      <div className="sec2">
        <h3>Important links</h3>
        <div className="flex">
          <div className="col">
            <a href="/">About us</a>
            <a href="/">Teams</a>
            <a href="/">Careers</a>
            <a href="/">Blogs</a>
          </div>
          <div className="col">
            <a href="/">Support</a>
            <a href="/">Investor relations</a>
            <a href="/">Terms and conditions</a>
            <a href="/">Privacy policy</a>
          </div>
          <div className="col">
            <a href="/">About us</a>
            <a href="/">Teams</a>
            <a href="/">Careers</a>
            <a href="/">Blogs</a>
          </div>
          <div className="col">
            <a href="/">Support</a>
            <a href="/">Investor relations</a>
            <a href="/">Terms and conditions</a>
            <a href="/">Privacy policy</a>
          </div>
        </div>
      </div>
      <div className="sec3">
        <div className="top">
          <div className="desc">
            <div className="logo">
              <img src={logoImg} alt="logo" />
            </div>
            <h3>World's leading chain of hotels and homes</h3>
          </div>
          <div className="icons">
            <div className="icon">
              <InstagramIcon />
            </div>
            <div className="icon">
              <FacebookIcon />
            </div>
            <div className="icon">
              <TwitterIcon />
            </div>
            <div className="icon">
              <YouTubeIcon />
            </div>
          </div>
        </div>
        <div className="bottom">
          <p className="copyright">© 2022 LOGO Inc</p>
        </div>
      </div>
    </div>
  );
};

export default Footer;
