import React, { useContext, useEffect, useState } from "react";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import DateRangePicker from "../DateRangePicker";
import { Link, useNavigate } from "react-router-dom";
import Autocomplete from "react-google-autocomplete";
import { LoginContext } from "../../Contexts/LoginContext";
import axios from "axios";
import { autocompleteClasses, Button, TextField } from "@mui/material";
import { API_GET_HOTELS_BY_LOCATION } from "../../apis";


const searchListData = [
  {
    id: "1",
    name: "Search result 1",
  },
  {
    id: "2",
    name: "Search result 2",
  },
  {
    id: "3",
    name: "Search result 3",
  },
  {
    id: "4",
    name: "Search result 4",
  },
  {
    id: "5",
    name: "Search result 5",
  },
  {
    id: "6",
    name: "Search result 6",
  },
  {
    id: "7",
    name: "Search result 7",
  },
  {
    id: "8",
    name: "Search result 8",
  },
  {
    id: "9",
    name: "Search result 9",
  },
  {
    id: "10",
    name: "Search result 10",
  },
];
const options = {
  types: ['restaurant', 'cafe', 'food', 'locality', 'neighborhood']
};
const apiKey = 'AIzaSyBTEUfTJuLWjTZ89JicwaA4SeaQv2TFg-k'; 

const SearchHotels = (props) => {
  const {searchResults, setSearchResults, searchParams, setSearchParams, setrange, range, rooms, setrooms, autocompletePlaceholder, setAutocompletePlaceholder}  = useContext(LoginContext);
  const navigate = useNavigate();
  const [search, setsearch] = useState("");
  const [searchList, setsearchList] = useState(searchListData);
  const [calendarVisible, setcalendarVisible] = useState(false);
  const [roomsCardVisible, setroomsCardVisible] = useState(false);

  function onSuggestionSlected (place) {
    searchParams.latitude =  place.geometry.location.lat();
    searchParams.longitude =  place.geometry.location.lng();
    // setSearchParams(searchParams);
    console.log(place);
    console.log(searchParams);
    setSearchParams(searchParams);
    const inputElement = document.getElementsByClassName("pac-target-input")[0];
    console.log(inputElement);
    setAutocompletePlaceholder(place.formatted_address);
  }
  function OnNearMeClick() {
    navigator.geolocation.getCurrentPosition(function (position) {
      console.log(position);
      console.log(searchParams);
      searchParams.latitude =  position.coords.latitude;
      searchParams.longitude =  position.coords.longitude;
      const url = `https://maps.googleapis.com/maps/api/geocode/json?latlng=${position.coords.latitude},${position.coords.longitude}&sensor=true&key=${apiKey}`;
      console.log(url);
      axios.get(url).then((response) => {
        console.log(response);
        const locationName = response?.data?.plus_code?.compound_code;
        setAutocompletePlaceholder(locationName);
      })
      setSearchParams(searchParams);
      axios.post(API_GET_HOTELS_BY_LOCATION, searchParams).then((response) => {
        setSearchResults(response.data.data);
      })
      navigate('/search');
      console.log(searchParams);
    }
    );
    // navigate("/search");
  }
  function onSetRange(value) {
    const startDate = value.startDate;
    const endDate = value.endDate;
    searchParams.from_date = startDate;
    searchParams.to_date = endDate;
    console.log("search params: ", searchParams);
    setSearchParams(searchParams);
    setrange(value);
  }
  useEffect(() => {
    updateSearchParams(rooms, rooms.length);
    function onScroll() {
      setsearchList([]);
    }
    document.addEventListener("scroll", onScroll);

    return () => {
      document.removeEventListener("scroll", onScroll);
    };
  }, []);

  useEffect(() => {
    if (search) {
      setsearchList(searchListData);
    } else {
      setsearchList([]);
    }
  }, [search]);

  useEffect(() => {
    if (calendarVisible || roomsCardVisible) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }
  }, [calendarVisible, roomsCardVisible]);

  function onAddRoom() {
    rooms.length < 5 && setrooms([...rooms, { roomNo: rooms.length + 1, personNo: 2 }]);
    updateSearchParams([...rooms, { roomNo: rooms.length + 1, personNo: 2 }], rooms.length+1);
  }
  function onDeleteRoom() {
    rooms.length > 1 && setrooms(rooms.slice(0, -1));
    updateSearchParams(rooms.slice(0, -1), rooms.length -1);
  }
  function addPerson(item, i) {
    const roomsCopy = [...rooms];
    roomsCopy[i].personNo = roomsCopy[i].personNo + 1;
    console.log(roomsCopy);
    setrooms(roomsCopy);
    updateSearchParams(roomsCopy, roomsCopy.length);
  }
  function deletePerson(item, i) {
    const roomsCopy = [...rooms];

    if (roomsCopy[i].personNo > 1) {
      roomsCopy[i].personNo = roomsCopy[i].personNo - 1;
      setrooms(roomsCopy);
      updateSearchParams(roomsCopy, roomsCopy.length);
    }
  }
  function updateSearchParams(roomCopy, roomLength) {
    const rooms_info = [];
    roomCopy.forEach((roomInfo) => {
      const obj = {
        no_of_adults: roomInfo.personNo
      }
      rooms_info.push(obj);
    })
    const totalRoom = roomLength;
    const totalPerson = getPersonNo(roomCopy);
    searchParams.room_count = totalRoom;
    searchParams.no_of_adults = totalPerson;
    searchParams.rooms_info = rooms_info;
    console.log("search params: ", searchParams);
    setSearchParams(searchParams);
  }

  function getPersonNo(roomCopy) {
    let personNo = 0;
    if(roomCopy && roomCopy.length) {
      roomCopy.forEach((item) => {
      personNo = personNo + item.personNo;
    });
    return personNo;
    }
    rooms.forEach((item) => {
      personNo = personNo + item.personNo;
    });

    return personNo;
  }
  function onSearchClick() {
    axios.post(API_GET_HOTELS_BY_LOCATION, searchParams).then((response) => {
      setSearchResults(response.data.data);
      navigate('/search');
    })
  }
  return (
    <div className="search-hotels">
      <div className="input">
        <Autocomplete
          apiKey={apiKey}
          onPlaceSelected={onSuggestionSlected}
          options={options}
          placeholder={autocompletePlaceholder}
          getdefaultvalue={'fdfd'}
        />
        <div className="near-me" onClick={() => OnNearMeClick()}>
          <LocationOnIcon />
          <p>Near me</p>
        </div>
        {/* {searchList && searchList.length ? (
          <div className="list">
            {searchList.map((item) => (
              <div key={item.id} className="item">
                <p>{item.name}</p>
              </div>
            ))}
          </div>
        ) : null} */}
      </div>
      <div className="date">
        <div className="date-btn" onClick={() => setcalendarVisible(!calendarVisible)}>
          <h3>
            {range.startDate.toLocaleDateString("default", { weekday: "long" }).slice(0, 3)} {range.startDate.getDate()}{" "}
            {range.startDate.toLocaleString("default", { month: "long" }).slice(0, 3)} -{" "}
            {range.endDate.toLocaleDateString("default", { weekday: "long" }).slice(0, 3)} {range.endDate.getDate()}{" "}
            {range.endDate.toLocaleString("default", { month: "long" }).slice(0, 3)}
          </h3>
        </div>
        {calendarVisible && <div className="bg-tr" onClick={() => setcalendarVisible(false)} />}

        {calendarVisible && (
          <div className="calendar-range">
            <DateRangePicker value={range} setvalue={onSetRange} />
          </div>
        )}
      </div>
      <div className="rooms">
        <div className="rooms-btn" onClick={() => setroomsCardVisible(!roomsCardVisible)}>
          <h3>
            {rooms.length} room, {getPersonNo()} guests
          </h3>
        </div>
        {roomsCardVisible && <div className="bg-tr" onClick={() => setroomsCardVisible(false)} />}

        {roomsCardVisible && (
          <div className="rooms-card">
            <div className="header">
              <h3>Rooms</h3>
              <h3>Guests</h3>
            </div>
            {rooms.map((item, i) => (
              <div key={item.roomNo} className="details">
                <p>Room {item.roomNo}</p>
                <div className="actions">
                  <div className="btn" onClick={() => deletePerson(item, i)}>
                    -
                  </div>
                  <p className="no">{item.personNo}</p>
                  <div className="btn" onClick={() => addPerson(item, i)}>
                    +
                  </div>
                </div>
              </div>
            ))}
            <div className="room-actions">
              <div className={`btn ${rooms.length === 1 ? "btn-disabled" : ""}`} onClick={onDeleteRoom}>
                Delete room
              </div>
              <div className={`btn ${rooms.length === 5 ? "btn-disabled" : ""}`} onClick={onAddRoom}>
                Add room
              </div>
            </div>
          </div>
        )}
      </div>
      <Button className="btn" onClick={() => onSearchClick()}>Search</Button>
      {/* <Link className="btn" to="/search">
        <p>Search</p>
      </Link> */}
    </div>
  );
};

export default SearchHotels;
